	module.exports = {"data":{"markdownRemark":{"html":"<p>User-Centered Design by Travis Lowdermilk is a solid overview of the principles and practices of designing a software project with a focus on your users.  The book does a good job of laying out the basic tools and processes of user-centered design, like usability studies, surveys, and project plans.  It also argues convincingly for the power  of its central points: user focus, preparation, and a structured process.</p>\n<p>If you’re looking for a deep dive into design theory, this is probably not a book you want.  Rather than dig into abstract design issues, this book instead acts as a survey of the user-centered design landscape, and points you to resources to go deeper if a particular topic catches your interest.  Lowdermilk also does a good job of pointing out examples of the different principles discussed, though for a stretch of the book he appears to be a bit fixated on a small selection of examples, particularly 53 Inc.’s popular iPad app <a href=\"https://itunes.apple.com/us/app/paper-by-fiftythree/id506003812?mt=8\">Paper</a>.</p>\n<p>I’d recommend this book for developers who want to open their minds to design considerations and improve their ability to make usable products.  It particularly seems suited for the engineer who knows technically how to implement features and write good code, but is still figuring out how to write applications that delight users.  I’d especially recommend it for team leads or independent developers who are more likely to be able to meet users and be a part of managing requirements.  I think all engineers could benefit from the books emphasis on the users point of view, but the real benefits will come to those who are able to make its suggestions part of the structure of their product workflows.</p>\n<p><a href=\"http://www.amazon.com/gp/product/1449359809/ref=as_li_tl?ie=UTF8&camp=1789&creative=390957&creativeASIN=1449359809&linkCode=as2&tag=benmccormicko-20&linkId=5XN7KS7FCBFHZ54O\">Buy User-Centered Design on Amazon (Affiliate Link)</a><img src=\"http://ir-na.amazon-adsystem.com/e/ir?t=benmccormicko-20&amp;l=as2&amp;o=1&amp;a=1449359809\" width=\"1\" height=\"1\" border=\"0\" alt=\"\" style=\"border:none !important; margin:0px !important;\"></p>\n<p>This review was part of the <a href=\"http://oreilly.com/bloggers/\">O’Reilly Blogger Program</a>.  I am not compensated for this review, but did receive a free review copy of the book.  The opinions expressed here are my own.</p>","frontmatter":{"title":"Book Review: User-Centered Design","keywords":"Book Review User-Centered Design","category":"reviews","date":"2013-08-12T15:15:00+00:00","path":"/2013/08/12/book-review-user-centered-design","layout":"post","hideFooter":null},"fields":{"slug":"/2013/08/12/book-review-user-centered-design"}}},"pathContext":{"slug":"/2013/08/12/book-review-user-centered-design","relatedPosts":[{"path":"/2012/12/20/sample-draft","data":{"title":"Sample Draft: Not a real post","path":"/2012/12/20/sample-draft","description":"Not a real post","date":"2012-12-20T13:00:00+00:00"}},{"path":"/2013/01/06/book-review-effective-javascript","data":{"title":"Book Review: Effective Javascript","path":"/2013/01/06/book-review-effective-javascript","description":"A book review of Effective Javascript by David Herman","date":"2013-01-06T22:00:00+00:00"}},{"path":"/2013/08/12/book-review-user-centered-design","data":{"title":"Book Review: User-Centered Design","path":"/2013/08/12/book-review-user-centered-design","description":"A book review of User-Centered Design by Travis Lowdermilk","date":"2013-08-12T15:15:00+00:00"}}]}}

